import React from "react";
import dialogsItems from "./dialogsItems";
import {connect} from "react-redux";


let mapStateToProps = (state) => {
    return {
        PublicChannels: state.dialogsPage.PublicChannels,
        PrivateChannels: state.dialogsPage.PrivateChannels,
        DirectMessage: state.dialogsPage.DirectMessage,
        profile: state.postPage.profile,
    }
}
let mapDispatchToProps = (dispatch) => {
    return {}
}


export default connect(mapStateToProps, mapDispatchToProps)(dialogsItems);