import React from 'react';
import {Field, reduxForm,reset} from "redux-form";
import s from "./../messagesItems.module.css"
import {Input} from "../../../common/formsControls/formsControls";
import {maxLengthCreator, required} from "../../../../utils/validations/validator";

const maxLength100=maxLengthCreator(100);

function AddNewMessageForm(props) {
    return (
        <form onSubmit={props.handleSubmit} className={s.footerForm}>
            <div className={s.footerText}>
                <Field placeholder={'Write something here...'} name={'newMessageBody'}
                       component={Input} className={s.footerInput}
                       validate={[required, maxLength100]}
                />
            </div>
            <div className={s.footerSend}>
                <button>Send</button>
            </div>
        </form>
    )
}

const afterSubmit = (result, dispatch) =>
    dispatch(reset('dialogAddMessageForm')); // после успешной отправки формы, обнуляем поля


const AddMessageReduxForm=reduxForm({form:'dialogAddMessageForm', onSubmitSuccess: afterSubmit,})(AddNewMessageForm);
export default AddMessageReduxForm




