import React from "react";
import s from "./../dialogsItems.module.css"
import {NavLink} from "react-router-dom";


    const DialogItem = (props) => {
        return <NavLink to={"/dialogs/" + props.id} className={s.dialog1}>
            <div className={s.Team_ava}>
                <img src={props.src} alt=''/>
            </div>
            <div className={s.Team_text}>
                <h6>{props.name}</h6>
                <p className={s.dialog_p}>{props.name2}</p>
            </div>
            <div className={s.Team_time}>
                <div className={s.time1 + ' ' + s +' '+props.active}>{props.time1}</div>
                <p className={s.time2}>{props.time2}</p>
            </div>
        </NavLink>
    }

export default DialogItem;