import React from "react";
import s from "./Dialogs.module.css";
import MessagesItemsContainer from "./Messages_items/MessagesItemsContainer";
import DialogsItemsContainer from "./Dialogs_items/DialogsItemsContainer";


const Dialogs = () => {
    return (
        <div className={s.dialogs}>
            <DialogsItemsContainer/>
            <MessagesItemsContainer/>
        </div>
    )
}

export default Dialogs;

