import React from "react";
import s from "./dialogsItems.module.css"
import DialogsInfo from "../Dialogs_info/dialogsInfo";
import DialogItem from "./dialogItem/dialogItem";


const DialogsItems = (props) => {

    let PublicChannels = props.PublicChannels.map(d => <DialogItem id={d.id}
                                                                   src={d.src}
                                                                   name={d.name}
                                                                   name2={d.name2}
                                                                   time1={d.time1}
                                                                   time2={d.time2}
                                                                   active={d.active}
                                                                   key={d.id}/>)
    let PrivateChannels = props.PrivateChannels.map(d => <DialogItem id={d.id}
                                                                     src={d.src}
                                                                     name={d.name}
                                                                     name2={d.name2}
                                                                     time1={d.time1}
                                                                     time2={d.time2}
                                                                     key={d.id}/>)
    let DirectMessage = props.DirectMessage.map(d => <DialogItem id={d.id}
                                                                 src={d.src}
                                                                 name={d.name}
                                                                 name2={d.name2}
                                                                 time1={d.time1}
                                                                 time2={d.time2}
                                                                 key={d.id}/>)
    return <div className={s.dialogs}>
        <DialogsInfo profile={props.profile}/>
        <div className={s.dialogs1}>
            <div className={s.public}>
                <div className={s.dialog_border}><h5>Public Channels</h5></div>
                {PublicChannels}
                <div className={s.dialog_border}><h5>Private Channels</h5></div>
                {PrivateChannels}
                <div className={s.dialog_border}><h5>Direct Message</h5></div>
                {DirectMessage}
            </div>
        </div>
    </div>
}

export default DialogsItems;